* {
    margin: 0;
    padding: 0;
    text-decoration: none;
    box-sizing: border-box;
  }
  .submit_active.container2__font {
    animation: container 1.5s forwards;
  }
  @keyframes container {
    from {
      transform: rotate3d(1, 67, 2, 95deg);
      left: 230px; 
      opacity:0}
    to {
      left:0;
      transform: rotate3d(0);;
      opacity:1}
  }

  .submit_active.modal2-content {
    animation: modal2 1.5s forwards;
  }
  @keyframes modal2 {
    from {
      transform: rotate3d(-1, 63, -1, 95deg);
      right: 227px;
      opacity:0}
    to {
      right:0;
      transform: rotate3d(0);;
      opacity:1}
  }
  .container-content {
    background: #f9f9f9;
    display: flex;
    padding: 65px 15px;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  
  .container2 {
    display: flex;
    flex-direction: row;
    background: #882772;
    padding: 10px;
    gap: 15px;
    width: 90%;;
  }
  .container2__font {
    width: 100%;
    position: relative;
  }
  .container2__font_img {
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: 0.5;
  }
  .container2__font_info {
    padding: 25px;
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  .container2__font_info_h1 {
    font-size: 22px;
    font-family: "Rieltor", Arial, sans-serif;
    line-height: 1.4;
    font-weight: 700;
    text-transform: uppercase;
    opacity: 0.95;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
    color: #ffffff;
  }
  .container2__font_info_p {
    color: #ffffff;
    font-size: 18px;
    font-family: "Rieltor", Arial, sans-serif;
    line-height: 1.4;
    font-weight: 400;
    opacity: 0.7;
    background-position: center center;
    border-color: transparent;
    border-style: solid;
    width: 85%;
  }
  
  .modal2-content {
    border-radius: 10px;
    padding: 10px;
    width: 100%;
    background: #ffffff;
  }
  .modal2-content__form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
    padding: 10px;
  }
  .modal2-content__form_select {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 5px;
  }
  .modal2-content__form_select_p {
    font-size: 14px;
    color: #505050;
    font-weight: 400;
    font-family: "Rieltor", Arial, sans-serif;
  }
  .modal2-content__form_select_option {
    width: 100%;
    height: 50px;
    font-size: 16px;
    color: #696969;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    font-weight: 400;
    font-family: "Rieltor", Arial, sans-serif;
    padding: 13px;
    border-radius: 10px;
  }
  .modal2-content__form_nameNumber {
    display: flex;
    justify-content: center;
    flex-direction: row;
    gap: 50px;
  }
  .modal2-content__form_name {
    width: 100%;
    height: 50px;
    font-size: 16px;
    color: #696969;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    font-weight: 400;
    font-family: "Rieltor", Arial, sans-serif;
    padding: 13px;
    border-radius: 10px;
  }
  .modal2-content__form_number {
    width: 100%;
    height: 50px;
    font-size: 16px;
    color: #696969;
    border: 1px solid #eeeeee;
    background-color: #ffffff;
    font-weight: 400;
    font-family: "Rieltor", Arial, sans-serif;
    padding: 13px;
    border-radius: 10px;
  }
  .modal2-content__form_check {
    display: flex;
    flex-direction: row;
    gap: 10px;
    padding: 10px;
  }
  .modal2-content__form_check_box:hover {
    cursor: pointer;
  }
  .modal2-content__form_check_p {
    font-size: 10px;
    color: #9a9a9a;
    font-weight: 400;
    font-family: "Rieltor", Arial, sans-serif;
  }
  .modal2-content__form_button {
    background-color: #882772;
    font-weight: 500;
    text-transform: uppercase;
    width: 100%;
    height: 54px;
    border-radius: 10px;
    color: #ffffff;
  }
  .modal2-content__form_button:hover {
    cursor: pointer;
    opacity: 0.5;
  }

  @media (max-width: 500px) {
    .container2 {
      flex-direction: column;
    }
    .container2__font_info {
      min-height: 400px;
    }
    .container2__font_info_h1 {
      font-size: 13px;
    }
    .container2__font_info_p {
      font-size: 10px;
      width: 100%;
    }
    .modal2-content__form_select_p {
      font-size: 12px;
    }
    .modal2-content__form_check_p {
      font-size: 7px;
    }

    .modal2-content__form_nameNumber {
      flex-direction: column;
    }

    .modal2-content__form_name {
      font-size: 12px;
    }

    .modal2-content__form_select_option {
      font-size: 12px;
    }
  }

  @media (min-width: 501px) and (max-width: 800px) {
    .container2 {
      flex-direction: column;
    }
    .container2__font_info {
      min-height: 400px;
    }
    .container2__font_info_h1 {
      font-size: 16px;
    }
    .container2__font_info_p {
      font-size: 12px;
      width: 100%;
    }
    .modal2-content__form_select_p {
      font-size: 14px;
    }
    .modal2-content__form_check_p {
      font-size: 9px;
    }
  }